@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&family=Source+Sans+Pro:wght@200;300;400;600;700&display=swap);
.recipe-container {
    display: flex;
    flex-wrap: wrap;
}

.home h1 {
    font-family: 'Playfair Display', serif;
    padding: 10% 5% 0 5%;
    font-weight: 400;
    text-align: center;
}

@media (min-width: 481px) {
    .home h1 {
        font-size: 3.8rem;
        padding-top: 7%;
    }
}

@media (min-width: 760px) {
    .recipe-container {
        justify-content: center;
    }
}

.recipe-ctn {
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    background-color: #ece9e0;
    width: 100%;
    margin: 3%;
    padding: 3%;
    box-shadow: 0 0 25px 0 #89886a;
    text-decoration: none;
    color: inherit;
}

.recipe-info {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    width: 50%;
}

.recipe-info h1 {
    font-size: 1rem;
    font-weight: 700;
    padding: 0;
}

.recipe-ctn img {
    height: 150px;
    width: 150px;
    border-radius: 10px;
}

.recipe-btn {
    border: 1px solid #2c3b28;
    color: #2c3b28;
    border-radius: 15px;
    padding: 5px 10px;
}

.food-img {
    height: 250px;
    width: 250px;
}

@media (min-width: 760px) {
    .recipe-ctn {
        width: 47%;
        margin: 1%;
    }
}

@media (min-width: 1025px) {
    .recipe-ctn {
        width: 30%;
        margin: 1%;
    }
}

.img {
    height: 100vh;
    width: 100vw;
    background-image: url(/recipe-app/static/media/start-mobile.f872bdd7.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
}

.img.clicked {
    left: -100%;
    transition: left 0.7s;
}

.img h1 {
    font-family: 'Playfair Display', serif;
    font-style: italic;
    font-size: 3rem;
    text-align: center;
    margin: auto;
    padding-top: 70%;
    font-weight: 100;
}

@media (min-width: 481px) {
    .img h1 {
        padding-top: 50%;
        font-size: 3.3rem;
    }
}

@media (min-width: 1000px) {
    .img h1 {
        font-size: 4.8rem;
    }
}

@media (min-width: 1025px) {
    .img {
        background-image: url(/recipe-app/static/media/start.7c5aeacc.jpg);
    }

    .img h1 {
        padding-top: 16%;
        padding-left: 45%;
    }
}

.option {
    margin: 10px;
    flex-shrink: 0;
    border-radius: 50%;
}

.option:hover {
    cursor: pointer;
}

.option.selected img {
    box-shadow: inset 0 0 0 2px #2c3b28;
    opacity: 0.5;
}

.option img {
    width: 105px;
    height: 105px;
    background-size: cover;
    border-radius: 50%;
}

.option h3 {
    padding-top: 8px;
    color: inherit;
    font-weight: 400;
    text-align: center;
    font-size: 15px;
}

@media (min-width: 481px) {
    .option img {
        width: 150px;
        height: 150px;
    }
    .option h3 {
        font-size: 22px;
    }
}

.option-title {
    display: flex;
    justify-content: center;
    font-weight: 600;
    flex-shrink: 0;
    padding: 2% 0;
    font-size: 1.5rem;
}

@media (min-width: 481px) {
    .option-title {
        font-size: 1.9rem;
    }
}

.form-ctn {
    padding-bottom: 8%;
    text-align: center;
}

.search-form {
    display: flex;
    justify-content: center;
    padding: 7% 0 4% 0;
}

.search-bar {
    height: 45px;
    border: none;
    width: 70%;
    border-radius: 7px;
    background-color: white;
    border: 1px solid #89886a;
    outline: none;
    padding-left: 10px;
    font-size: 1.3rem;
    font-family: 'Playfair Display', serif;
}

.search-btn {
    height: 45px;
    border: 1px solid #89886a;
    color: #89886a;
    background-color: white;
    margin-left: 1%;
    border-radius: 7px;
    width: 15%;
}

.option-ctn {
    display: flex;
    align-items: center;
    overflow: scroll;
}

.option-ctn::-webkit-scrollbar {
    display: none;
}

hr {
    width: 60%;
    border: 1px solid #2c3b28;
    margin: 3% auto;
}

@media (min-width: 481px) {
    .search-form {
        padding: 5% 0 3% 0;
    }
    .search-bar {
        width: 50%;
        height: 50px;
    }

    .search-btn {
        width: 8%;
        height: 50px;
    }

    .option-ctn {
        display: inline-flex;
        max-width: 100%;
        overflow: scroll;
    }
}

@media (min-width: 1025px) {
    .search-bar {
        width: 45%;
    }
    .search-btn {
        width: 5%;
    }
}

.arrow-icon {
    font-size: 2.7rem;
    color: #2c3b28;
    position: absolute;
    padding: 2%;
    z-index: 1;
}

.full-recipe-info {
    text-align: center;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    margin-top: -20px;
    position: relative;
    background-color: #ece9e0;
}

.full-recipe-title {
    padding-top: 5%;
}

.label-ctn {
    display: flex;
    justify-content: space-evenly;
    padding-top: 5%;
}

.label {
    border-radius: 10px;
    padding: 2%;
    box-shadow: 0 0 60px 0 #89886a;
}

.nutrient-ctn {
    display: flex;
    justify-content: space-evenly;
}

.full-recipe-info h2 {
    font-size: 1.5rem;
    padding: 5% 0 3px 0;
}

@media (min-width: 481px) {
    .arrow-icon {
        font-size: 4rem;
    }

    .full-recipe-info {
        margin: 0;
    }

    .full-recipe-title {
        font-size: 2.4rem;
        padding-top: 2%;
    }

    .full-recipe-info h2 {
        font-size: 1.9rem;
        padding: 5% 0 2% 0;
    }

    .label-ctn {
        width: 80%;
        margin: auto;
    }

    .label {
        font-size: 1.3rem;
        box-shadow: 0 0 70px 0 #89886a;
    }

    .nutrient-ctn {
        padding: 0 2%;
    }
}

@media (min-width: 769px) {
    .arrow-icon {
        font-size: 5.5rem;
    }

    .full-recipe-title {
        font-size: 3rem;
        padding-top: 2%;
    }

    .full-recipe-info h2 {
        font-size: 2.5rem;
    }
}

@media (min-width: 1025px) {
    .arrow-icon {
        left: 0;
        top: 0;
    }

    .full-recipe {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
    }

    .full-recipe-info {
        width: 40%;
        text-align: left;
        height: 400px;
    }

    .full-recipe-title {
        font-size: 1.7rem;
        padding-bottom: 1%;
    }

    .full-recipe-info h2 {
        font-size: 1.2rem;
        padding: 4% 0 2% 0;
    }

    .label-ctn {
        width: 100%;
        justify-content: left;
        background-color: #ece9e0;
        padding: 2% 0;
    }

    .label {
        font-size: 0.8rem;
        margin: 0 2% 0 0;
        box-shadow: 0 0 70px 0 #89886a;
    }

    .nutrient-ctn {
        justify-content: left;
        padding: 0;
    }
}

.full-recipe-header {
    position: relative;
}

.full-recipe-header img {
    width: 100%;
}

.instruction-btn {
    font-family: 'Playfair Display', serif;
    border: 1px solid #2c3b28;
    color: #2c3b28;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 15px;
    padding: 5px 8px;
    position: absolute;
    bottom: 8%;
    right: 10px;
}

@media (min-width: 481px) {
    .full-recipe-header {
        padding: 10% 0 3.5% 0;
    }

    .full-recipe-header img {
        width: 50%;
        margin: auto;
        border-radius: 20px;
    }

    .instruction-btn {
        padding: 5px 13px;
        font-size: 0.9rem;
        right: 26%;
    }
}

@media (min-width: 769px) {
    .instruction-btn {
        padding: 8px 15px;
        font-size: 1rem;
    }
}

@media (min-width: 1025px) {
    .full-recipe-header {
        margin-right: 3%;
        padding: 0;
    }

    .full-recipe-header img {
        width: 400px;
    }

    .instruction-btn {
        right: 2%;
        font-size: 0.9rem;
        padding: 5px 12px;
        bottom: 10px;
    }

    .instruction-btn:hover {
        cursor: pointer;
    }
}

.nutrient-ctn {
    display: flex;
}

.nutrient {
    border-radius: 10px;
    padding: 2%;
    box-shadow: 0 0 60px 0 #89886a;
    width: 25%;
    margin: 2%;
    text-align: center;
}

@media (min-width: 481px) {
    .nutrient {
        box-shadow: 0 0 70px 0 #89886a;
        font-size: 1.3rem;
        padding: 1%;
    }
}

@media (min-width: 1025px) {
    .nutrient {
        box-shadow: 0 0 70px 0 #89886a;
        font-size: 0.8rem;
        padding: 1%;
        width: 15%;
        margin: 0 2% 0 0;
    }
}

.ingredient-list {
    display: inline-flex;
    overflow: scroll;
    max-width: 100%;
    padding-bottom: 3%;
}

.ingredient-list::-webkit-scrollbar {
    display: none;
}

.ingredient-img {
    width: 70px;
    border-radius: 50%;
    padding-bottom: 6%;
}

.ingredient-list li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
}

@media (min-width: 481px) {
    .ingredient-img {
        width: 90px;
    }

    .ingredient {
        font-size: 1.3rem;
    }
}

@media (min-width: 769px) {
    .ingredient-img {
        width: 120px;
    }

    .ingredient {
        font-size: 1.3rem;
    }
}

@media (min-width: 1025px) {
    .ingredient-img {
        width: 60px;
    }

    .ingredient-list {
        display: flex;
        flex-wrap: wrap;
    }

    .ingredient {
        text-align: center;
        font-size: 0.8rem;
    }

    .ingredient-list li {
        margin: 0 10px 10px 0;
        width: 80px;
    }
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Playfair Display', serif;
    background-color: #ece9e0;
    color: #2c3b28;
}

img {
    display: block;
}

